<template>
  <v-bottom-navigation
    v-model="bottomNav" :height = "70"
  >
    <v-btn value="parent" :bottom="true">
      <span>Parent Zone</span>
      <v-icon size="40">mdi-human-child</v-icon>
    </v-btn>

    <v-btn value="student">
      <span>Student Zone</span>
      <v-icon size="25">mdi-human-child</v-icon>
    </v-btn>

  </v-bottom-navigation>
</template>

<script>
  export default {
    data () {
      return {
        bottomNav: 'parent',
      }
    },
    watch: {
        bottomNav : function() {
           this.$store.commit('changeBottomBarValue',this.bottomNav);
        }
    }
  }
</script>